@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap");
:root {
  --primary-font:"Quicksand", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-bg: #1D2123;
  --grey: #FFFFFF1C;
  --darker-bg: #1A1E1F;
  --yellow: #FACD66;
  --card-bg:#609EAF;
  --glow-yellow: 0px 0px 11px rgba(250, 205, 102, 0.25);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--primary-font);
  background-color: var(--dark-bg);
  overflow-x: hidden !important;
}

.overflow-hide {
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

@keyframes slide-in {
  from {
    transform: translateX(400px);
    visibility: hidden;
  }
  to {
    transform: translateX(5px);
    visibility: visible;
  }
}
@keyframes slide-out {
  from {
    transform: translateX(5px);
    visibility: visible;
  }
  to {
    transform: translateX(400px);
    visibility: hidden;
  }
}
.slide-in {
  animation: slide-in 0.3s ease-in-out forwards;
}

.slide-out {
  animation: slide-out 0.3s ease-in-out forwards;
}

.nav {
  width: 100vw;
}
.nav__search input:focus {
  outline: none;
}
@media (min-width: 64em) {
  .nav__search {
    position: absolute;
    left: 9.375rem;
    bottom: 1.25rem;
  }
}
.nav .mobile-nav {
  visibility: hidden;
  background-color: var(--darker-bg);
  max-width: 370px;
  min-width: 100%;
  height: 85vh;
  overflow: hidden;
  z-index: 99;
  transform: translateX(400px);
  transition: all 1s ease-in-out;
}
.nav .mobile-nav li {
  display: flex;
  font-size: 1.15rem;
  font-weight: 500;
  width: 100%;
  align-items: center;
  gap: 25px;
  cursor: pointer;
  opacity: 0.7;
  vertical-align: middle;
  transition: all 300ms ease-in-out;
}
.nav .mobile-nav li:not(:last-child) {
  margin-bottom: 50px;
}
.nav .mobile-nav li svg * {
  transition: all 200ms ease-in-out;
}
.nav .mobile-nav li * {
  align-self: center;
}
.nav .mobile-nav li:hover {
  opacity: 1;
}
.nav .mobile-nav li:hover svg * {
  opacity: 1;
  fill: var(--yellow) !important;
}

.hide {
  display: none !important;
}

.aside__itemsCtn {
  background-color: var(--darker-bg);
}
.aside__itemsCtn li svg {
  cursor: pointer;
}
.aside__itemsCtn li svg * {
  transition: all 200ms ease-in-out;
}
.aside__itemsCtn li svg:hover * {
  opacity: 1;
  fill: var(--yellow) !important;
}

.hero {
  overflow-x: hidden;
}
.hero__card {
  flex: 1 1 60%;
  border-radius: 2.5rem;
  background-color: var(--card-bg);
  background-repeat: no-repeat;
}
@media (max-width: 63.9375em) {
  .hero__card {
    border-radius: 1.25rem;
  }
}
@media (max-width: 63.9375em) {
  .hero__card {
    background-image: url(/assets/images/mb-spiral.png);
    background-position: 100% -30%;
  }
}
@media (min-width: 64em) {
  .hero__card {
    background-image: url(/assets/images/spiral.png);
    background-size: 80%;
    background-position: 160% 100%;
  }
}
@media (max-width: 63.9375em) {
  .hero__card .left .header_text {
    margin-top: 10.9375rem;
  }
}
@media (min-width: 64em) {
  .hero__card .left .likesCtn {
    margin-top: 5rem;
  }
}
@media (max-width: 63.9375em) {
  .hero__card .left .likesCtn {
    margin-top: 0.625rem;
  }
}
.hero__card .left .likesCtn > img {
  width: 40px;
  height: 40px;
}
.hero__card .left .likesCtn > img:not(:first-child) {
  margin-left: -15px;
}
.hero__topchart {
  flex: 1 1 35%;
}
.hero__topchart .my-container {
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .hero__topchart .my-container {
    overflow-x: scroll;
  }
}
.hero__topchart .my-container::-webkit-scrollbar {
  display: none;
}
.hero__topchart .my-container .topchartCtn {
  min-width: 100%;
  max-width: 26.0625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  background-color: var(--darker-bg);
  cursor: pointer;
  transition: all 300ms ease-in-out;
  border-radius: 1.25rem;
}
@media (min-width: 64em) {
  .hero__topchart .my-container .topchartCtn:hover {
    transform: scale(1.05);
  }
}
@media (max-width: 63.9375em) {
  .hero__topchart .my-container .topchartCtn {
    margin-right: 0.9375rem;
    padding: 0.9375rem;
  }
}
.hero__topchart .my-container .topchartCtn .left > * {
  margin-bottom: 20px;
}
.hero__topchart .my-container .topchartCtn .right > img {
  width: 2.2rem;
  border: 2px solid var(--grey);
  padding: 7px;
  border-radius: 100px;
}
@media (max-width: 63.9375em) {
  .hero__topchart .my-container .topchartCtn .right > img {
    position: absolute;
    top: 30px;
    right: 20px;
  }
}

.new-releases {
  height: auto;
}
.new-releases__cardsCtn {
  overflow-x: scroll;
  width: 100%;
}
.new-releases__cardsCtn::-webkit-scrollbar {
  display: none;
}
.new-releases__cardsCtn .card {
  min-width: 9.5625rem;
}
.new-releases__cardsCtn .card:not(:last-child) {
  margin-right: 1.875rem;
}
.new-releases__cardsCtn .card img {
  width: 100%;
  border-radius: 1.5625rem;
  margin-bottom: 0.625rem;
}
.new-releases__cardsCtn .card p:last-child {
  opacity: 0.7;
  padding-top: 0.1875rem;
}

.popular-section {
  height: auto;
}
.popular-section__cardsCtn {
  overflow-x: scroll;
  width: 100%;
}
.popular-section__cardsCtn::-webkit-scrollbar {
  display: none;
}
.popular-section__cardsCtn .card {
  min-width: 9.5625rem;
}
.popular-section__cardsCtn .card:not(:last-child) {
  margin-right: 1.875rem;
}
.popular-section__cardsCtn .card img {
  width: 100%;
  border-radius: 1.5625rem;
  margin-bottom: 0.625rem;
}
.popular-section__cardsCtn .card p:last-child {
  opacity: 0.7;
  padding-top: 0.1875rem;
}